import React, { createContext, useState, useEffect } from "react";
import axios from "axios";
// import { getLocalItem } from "../components/helpers/authService";
import jwtDecode from "jwt-decode";
import { httpGet } from "../../helper/httpMethods";
import { hideLoader, showLoader } from "../../helper/loader";
export const UserDataContext = createContext();

export const UserDataProvider = (props) => {
  const [user, setUser] = useState();
  const [userActive, setUserActive] = useState(false);
  const [count, setCount] = useState(0);
  const [firstTimeLoad, setFirstTimeLoad] = useState(true);
  let token = localStorage.getItem("token");
  useEffect(() => {
    checkUserState();
  }, []);

  const checkUserState = () => {
    if (token === null || token === undefined || token === "") {
      setUserActive(false);
    } else {
      ValidateToken();
    }
  };

  const ValidateToken = () => {
    if (jwtDecode(token).exp < Date.now() / 1000) {
      console.log("TOKEN EXPIRED");
      return;
    }
    getUserProfile();
    setUserActive(true);
  };
  const getUserProfile = async () => {
    showLoader();
    setFirstTimeLoad(true);
    const res = await httpGet(`auth/user`);
    if (res) {
      setFirstTimeLoad(false);
      hideLoader();
      if (res.er) {
        return;
      }
      console.log(res);
      setUser(res.user);
    }
  };
  return (
    <UserDataContext.Provider
      value={{ userActive, checkUserState, firstTimeLoad, user }}
    >
      {props.children}
    </UserDataContext.Provider>
  );
};
